.top_line {
  top: 0;
 position: relative;
 position: absolute;
 left:0;
  width: 100%;
  height: 104px;
  background-color: $main_color;
  border-bottom: 1px dotted #998e10;
  z-index: 135;
  @include transition(all, 0.5s, ease);
  // animation-name: anim;
  // animation-duration: 4s;
  // animation-iteration-count: 1;
  // animation-fill-mode: backwards;
  @include transition(all, 1.2s, ease);
}

.top_line.fixed {
  display: block;
  background-color: #222;
  width: 100%;
    height: 90px;
  position: fixed;
  animation-name: anim;
  animation-duration: 4s;
  // animation-iteration-count: 1;
  animation-fill-mode: both;
}

.top_line.fixed .top_wrapper {
    height: 90px;
}

@keyframes anim {
  from {
    opacity: 0;
    transform: translate3d(0, -100%, 0);
  }
  to {
    opacity: 1;
    transform: none;
  }
}
@keyframes anima {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;

  }
}
@keyframes anima {
  from {
    opacity: 1;
    transform: translate3d(0, -100%, 0);
  }

  to {
    opacity: 0;
transform: none;


  }
}















