.top_wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  height: 104px;
}

.top_menu ul {
  padding: 0;
  list-style: none;
}

.top_menu ul {
  font-size: 0;
}

.top_menu ul li {
  display: inline-block;
}

.top_menu ul li a {
  padding: 5px 15px;
  display: inline-block;
  color: #ffffff;
  font-size: 14px;
  font-weight: 400;
  text-transform: uppercase;
  text-decoration: none;
  @include trans;
  position: relative;
}

.top_menu ul li a:before {
  top: 50%;
  left: 0;
  content: '';
  width: 1px;
  height: 17px;
  position: absolute;
  display: inline-block;
  transform: translateY(-50%);
  background-color: #bd840a;
}

.top_menu ul li a:first-child:before {
  display: none;

}

.top_menu ul li a:hover {
  color: darken(#fff, 10%);
}

